.fordropdown{
    position: absolute;
    top: 100%;
    width: 100%;
    max-height: 233px;
    overflow: auto;
}
.forwholediv{
    position: relative;
}
.fordropdownelement{
    width: 100%;
    cursor: pointer !important;
    background-color: blueviolet;
    border: 1px solid;
    color: white;
    &:hover{
        background-color: white;
        color: blueviolet;
    }
}
.forelementwithlabel{
    display: flex;
    flex-direction: column;
    &>input{
        padding: 13px;
    }
}
